import { createStyles, Grid, Paper, WithStyles } from '@material-ui/core'
import { Theme, withStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'
import distanceInWords from 'date-fns/distance_in_words'
import React from 'react'
import _404 from '../components/404'
import Query from '../components/Query'
import { ACTIVITIES } from '../graphql/query/remote'

const styles = (theme: Theme) =>
  createStyles({
    root: {
      minHeight: '100vh!important',
    },
    bullet: {
      padding: theme.spacing.unit * 2,
    },
  })

interface IActivity extends WithStyles<typeof styles> {
  user?: any
}

const Activity = withStyles(styles)((props: IActivity) => {
  const { classes, user } = props
  const Bullet = () => <span className={classes.bullet}>â€¢</span>
  return (
    <Query
      query={ACTIVITIES}
      noError={true}
      variables={{
        limit: 50,
      }}
    >
      {({ loading, data, error }: IGraphqlData) => {
        const { me }: any = props
        if (error || !me || !me.stockPermission) {
          return <_404 />
        }
        return (
          <Paper className={classes.root}>
            <Grid container={true} direction='column' alignItems='center'>
              <Grid item={true} className='pt2'>
                {data.activities && data.activities.length ? (
                  data.activities.map((a: any) => {
                    return (
                      <React.Fragment key={a.id}>
                        <Typography variant='subheading' key={a.id}>
                          <Bullet />
                          <b>
                            {a.user.username === user.username
                              ? 'You'
                              : a.user.username}
                          </b>{' '}
                          performed <b>{a.activity}</b>{' '}
                          {distanceInWords(a.createdAt, new Date())} ago.
                        </Typography>
                        <hr />
                      </React.Fragment>
                    )
                  })
                ) : (
                  <Typography variant='subheading'>
                    <Bullet /> no activity yet
                  </Typography>
                )}
              </Grid>
            </Grid>
          </Paper>
        )
      }}
    </Query>
  )
})

export default Activity
